:root {
    --color-1: #fff;
    --color-3: #ee7347;
}

header {
    margin-top: 0;

    p {
        margin-top: -1em;
        text-transform: uppercase;
        color: var(--color-3);
        letter-spacing: 0.1em;
    }
}

h1 {
    text-transform: none;
}

img, figure {
    margin-left: auto;
    margin-right: auto;
}

figcaption {
    margin-top: 1em;
}

section {
    section + section {
        margin-top: 2em;
    }
}

#info {
    font-style: italic;
    p + p {
        margin-top: 0;
    }
}